"use client";

import { LanguageContext } from "./LanguageContext";
import { useContext, useState, useEffect } from "react";
import { getCookie, deleteCookie } from "../api/cookies.js";
import { Translation } from "./translation";
import Link from "next/link";
import "../css/main.css"

export default function Header() {
    const { language, toggleLanguage } = useContext(LanguageContext);
    const [isLoggedIn, setIsLoggedIn] = useState(false);
    const t = Translation[language];

    useEffect(() => {
        const loggedIn = getCookie("isLoggedIn") === "true";
        setIsLoggedIn(loggedIn);
    }, []);

    const handleLogout = async () => {
        const confirmLogout = window.confirm(t.logoutConfirm || "Are you sure you want to log out?");
        if (confirmLogout) {
            const response = await fetch('/api/auth/logout', {
                method: 'POST',
            });

            if (response.ok) {
                deleteCookie('isLoggedIn');
                deleteCookie('token');
                deleteCookie('refreshToken');
                setIsLoggedIn(false);
                window.location.href = '/';
            }
        }
    };

    return (
        <div className="header-container">
            <header>
                <div className="navbar">
                    <nav>
                        <div className="logo-box">
                            <a href="/"><img 
                                src='/imgs/logo.ico' 
                                alt={language === 'ar' ? "شعار المدرسة" : "School icon"} 
                                className="logo-img" 
                            /></a>
                        </div>
                        <NavUl t={t} handleToggleLang={toggleLanguage} isLoggedIn={isLoggedIn} handleLogout={handleLogout} />
                    </nav>
                </div>
            </header>
            <BurgerNav t={t} handleToggleLang={toggleLanguage} isLoggedIn={isLoggedIn} handleLogout={handleLogout} />
        </div>
    );
}

function NavUl({ t, handleToggleLang, isLoggedIn, handleLogout }) {
    return (
        <ul>
            <li><a href="/#home">{t.home}</a></li>
            <li><a href="/#about">{t.about}</a></li>
            <li><a href="/#skills">{t.skills}</a></li>
            <li><a href="/#contactus">{t.contactus}</a></li>
            {isLoggedIn ? (
                <li>
                    <button onClick={handleLogout}>{t.logout}</button>
                </li>
            ) : (
                <>
                    <li>
                        <Link href="/login">{t.login}</Link>
                    </li>
                    <li>
                        <Link href="/register">{t.register}</Link>
                    </li>
                </>
            )}
            <div id="lang" onClick={handleToggleLang}>
                <h3>{t.lang}</h3>
            </div>
        </ul>
    );
}

function BurgerNav({ t, handleToggleLang, isLoggedIn, handleLogout }) {
    const [isOpen, setIsOpen] = useState(false);

    const toggleIsOpen = () => {
        setIsOpen(prevIsOpen => !prevIsOpen);
    };

    return (
        <div className="burger">
            <button
                id="burgerBtn"
                className={isOpen ? "open checked" : "closed"}
                onClick={toggleIsOpen}
            >
                <span></span>
                <span></span>
                <span></span>
            </button>
            {isOpen && (
                <div className="burger-nav">
                    <ul>
                        <li>
                            <a href="#home">
                                <img src='/icons/house-solid.svg' alt="Home Icon" /> {t.home}
                            </a>
                        </li>
                        <li>
                            <a href="#about">
                                <img src='/icons/address-card-solid.svg' alt="About Icon" /> {t.about}
                            </a>
                        </li>
                        <li>
                            <a href="#skills">
                                <img src='/icons/brain-solid.svg' alt="Skills Icon" /> {t.skills}
                            </a>
                        </li>
                        <li>
                            <a href="#contactus">
                                <img src='/icons/users-solid.svg' alt="Contact Icon" /> {t.contactus}
                            </a>
                        </li>
                        {isLoggedIn ? (
                            <li>
                                <button onClick={handleLogout}>{t.logout}</button>
                            </li>
                        ) : (
                            <>
                                <li>
                                    <Link href="/login">{t.login}</Link>
                                </li>
                                <li>
                                    <Link href="/register">{t.register}</Link>
                                </li>
                            </>
                        )}
                        <div id="lang" onClick={handleToggleLang}>
                            <h3>{t.lang}</h3>
                        </div>
                    </ul>
                </div>
            )}
        </div>
    );
}