"use client";

import { useContext } from "react";
import { LanguageContext } from "./LanguageContext";
import { Translation } from "./translation";

export default function Footer() {
    const { language } = useContext(LanguageContext);
    const t = Translation[language];

    return (
        <footer>
            <div id="quick">
                <div className="aboutus">
                    <h3>{t.aboutUs}</h3>
                    <p>{t.schoolDescription}</p>
                </div>
                <div className="quicklinks">
                    <h3>{t.quickLinks}</h3>
                    <ul>
                        <li><a href="/#home">{t.home}</a></li>
                        <li><a href="/#about">{t.about}</a></li>
                        <li><a href="/#skills">{t.skills}</a></li>
                        <li><a href="/#contactus">{t.contactus}</a></li>
                    </ul>
                    <div>
                        <a href="https://www.facebook.com/share/g/194v4Ry7qw/" target="_blank" rel="noopener noreferrer">
                            <img src="/icons/facebook-brands.svg" alt="FaceBook" />
                        </a>
                        <a href="https://maps.app.goo.gl/isxUtNNcwRhijkTa6" target="_blank" rel="noopener noreferrer">
                            <img src="/icons/location-dot-solid.svg" alt="School location" />
                        </a>
                    </div>
                </div>
                <div className="contactwithus">
                    <h3>{t.contactus}</h3>
                    <p><b>{t.phone}: 01008118416</b></p>
                    <p><b>{t.email}: Moh.moussa.74@gmail.com</b></p>
                </div>
            </div>
            <div className="footer">
                <h5>
                    © 2025 | {t.allRightsReserved} | {t.createdBy}:{" "}
                    <b title={`${t.phone}: 01067370955, ${t.email}: yasherif92@gmail.com`}>
                        <u><i>{`"${t.programmer}"`}</i></u>
                    </b>
                </h5>
            </div>
        </footer>
    );
}